import React from "react";
import clsx from "clsx";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import List from "@material-ui/core/List";
import CssBaseline from "@material-ui/core/CssBaseline";
import Typography from "@material-ui/core/Typography";
import Divider from "@material-ui/core/Divider";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import InboxIcon from "@material-ui/icons/MoveToInbox";
import MailIcon from "@material-ui/icons/Mail";

import SiteHome from '../pages/site/SIteHome';
import SiteDetails from '../pages/site/SiteDetails';
import SiteReport from '../pages/site/SiteReport';
import { useHistory } from "react-router-dom";
import logo from '../assets/Kumar_Electricals_Png.png';
import AccountHome from '../pages/account/AccountHome';
import AccountHistory from "../pages/account/AccountHistory";



const routes = [
    {
      name: 'Home',
      path: "/accountHome",
    //   exact: true,
      sideName: 'Document upload',
    },
    // {
    //   name: 'Details',
    //   path: "/siteDetails",
    //   sideName: 'Return',
    // },
    {
      name: 'History',
      path: "/accountHistory",
      sideName: 'Uploaded history',
    }
  ];

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex"
  },
  appBar: {
    backgroundColor: "#ff6666",
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen
    })
  },
  appBarShift: {
    backgroundColor: "#ff6666",
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen
    })
  },
  menuButton: {
    marginRight: 36
  },
  hide: {
    display: "none"
  },
  drawer: {
    
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: "nowrap",
    backgroundColor: "#ff0000"
  },
  drawerOpen: {
    backgroundColor: "#ff8080",
    width: drawerWidth,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen
    })
  },
  drawerClose: {
    backgroundColor: "#ff6666",
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen
    }),
    overflowX: "hidden",
    width: theme.spacing(7) + 1,
    [theme.breakpoints.up("sm")]: {
      width: theme.spacing(9) + 1
    }
  },
  toolbar: {
    
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-end",
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3)
  },
  side: {
    backgroundColor: "#ff0000"
  }
}));

export default function AccountDashboard(props) {
  const classes = useStyles();
  const theme = useTheme();
  const history = useHistory();
  const [open, setOpen] = React.useState(true);
  const [fragment, setfragment] = React.useState(0);
  const [selectedIndex, setSelectedIndex] = React.useState(0);

  console.log( 'state'+ props.location.state);
console.log(history.location.state.userName);


  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

 const handleLogout = () => {
    console.log('hi hello how are you?')
    history.push("/");
  }

  // async function handleLogout() {
  //   await Auth.signOut();
  
  //   userHasAuthenticated(false);
  
  //   history.push("/login");
  // }

  const loadFragment = () => {
      switch (fragment) {
          case 0: return <AccountHome userDetails={history.location.state}/>
          case 1: return <AccountHistory userDetails={history.location.state.userName}/>
          case 2: return <SiteReport userDetails={history.location.state}/>
      }
  }


const handleListItemClick = (event, index) => {
    setSelectedIndex(index);
    setfragment(index)
    // console.log(index);
    // console.log(event.target.innerHTML);
  };


  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar
        position="fixed"
        className={clsx(classes.appBar, {
          [classes.appBarShift]: open
        })}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            className={clsx(classes.menuButton, {
              [classes.hide]: open
            })}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" noWrap  style={{fontWeight: '900'}}>
            ACCOUNT MODULE
          </Typography>
          {/* <Typography  style={{ width: '100%',}}>
          </Typography>
          <Typography  style={{ width: '100%',}}>
          </Typography>
          <Typography  style={{ width: '100%',}}>
          </Typography>
          <Typography  style={{ width: '100%',}}>
          </Typography>
          <Typography  style={{ width: '100%',}}>
          </Typography>
          <Typography  style={{ width: '100%',}}>
          </Typography>
          <div variant="h6" noWrap  style={{fontWeight: '900', width: '100%', justifyContent: 'flex-end', alignItems: 'flex-end'}}>
            <IconButton
              color="inherit"
              // aria-label="open drawer"
              // edge="end"
              // style={{ justifyContent: 'flex-end', alignItems: 'flex-end'}}
              onClick={handleLogout}
              // className={clsx(open && classes.hide)}
            >
              <MenuIcon />
            </IconButton>
          </div> */}
        </Toolbar>
      </AppBar>
      <Drawer
        variant="permanent"
        className={clsx(classes.drawer, {
          [classes.drawerOpen]: open,
          [classes.drawerClose]: !open
        })}
        classes={{
          paper: clsx({
            [classes.drawerOpen]: open,
            [classes.drawerClose]: !open
          })
        }}
      >
        <div className={classes.toolbar}>
          {/* <div style={{color: '#ffffff', fontSize: 20,fontWeight: '900'}}>Kumar Electricals</div> */}
          <img src={logo} width="170" height="50"/>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === "rtl" ? (
              <ChevronRightIcon />
            ) : (
              <ChevronLeftIcon />
            )}
          </IconButton>
        </div>
        <Divider />
        <List>
          {routes.map((text, index) => (
            <ListItem  button key={text.name} 
            selected={selectedIndex === index}
            // onClick={e=>setfragment(text.name)}
            onClick={(event) => handleListItemClick(text.name, index)}
            >
              <ListItemIcon style={{color: '#ffffff',}}>
                {index % 2 === 0 ? <InboxIcon /> : <MailIcon />}
              </ListItemIcon>
              {/* <ListItemText primary={text} /> */}
              {/* <Link to={text.path}><ListItemText primary={text.name} /></Link> */}
              <ListItemText primary={text.sideName} style={{color: '#ffffff',}}/>
            </ListItem>
          ))}
        </List>
        <Divider />
      </Drawer>
      <main className={classes.content}>
        <div className={classes.toolbar} />
          {loadFragment()}
      </main>
    </div>
  );
}



