function generate() {

    console.log('dhgs');
    let resStatus = 0
    if(props.userDetails.role === '3') {
    fetch('http://keproject-env.eba-i6rhizxy.ap-south-1.elasticbeanstalk.com/PurchesOrder/genaratePOCode', {
        method: 'GET',
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
      })
        .then(response => {
            resStatus = response.status
            console.log(response)
            return response.text()
          }
            // response.json()
            )
        .then(responseJson => {
          console.log(responseJson);
          switch (resStatus) {
            case 200: setpoNumber(responseJson);
            break
            default: console.log('error');
            break
          }
        })
        .catch(error => {
          console.error(error);
        });
      } else {

        
        fetch('http://keproject-env.eba-i6rhizxy.ap-south-1.elasticbeanstalk.com/QuotationOrderHeader/quetationCode', {
        method: 'GET',
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
      })
        .then(response => {
            resStatus = response.status
            console.log(response)
            return response.text()
          }
            // response.json()
            )
        .then(responseJson => {
          console.log(responseJson);
          switch (resStatus) {
            case 200: setpoNumber(responseJson);
            break
            default: console.log('error');
            break
          }
        })
        .catch(error => {
          console.error(error);
        });
      }

}

