
import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import SiteIndent from '../../components/SiteIndent';
import SiteIndentMaterial from '../../components/SiteIndentMaterial';
import SiteOutput from '../../components/SiteOutput';

import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import { PDFDownloadLink, Document, Page } from '@react-pdf/renderer'
// import MyDocument

import PurchaseHeader from '../../components/purchasecomponent/PurchaseHeader';
import PurchaseMaterial from '../../components/purchasecomponent/PurchaseMaterial';
import PurchaseOutput from '../../components/purchasecomponent/PurchaseOutput';



const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
  },
  button: {
    marginRight: theme.spacing(2),
  },
  instructions: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
}));

function getSteps() {
  return ['Header Fields', 'Working Fields', 
  'Output'
];
}






export default function PurchaseOrder(props) {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const [skipped, setSkipped] = React.useState(new Set());
  const [dataFlag, setDataFlag] = React.useState(false)
  const steps = getSteps();
  const [headerData2, setheaderData] = React.useState();
  const [bodyData, setbodyData] = React.useState();
  const [subTotalData, setsubTotalData] = React.useState();



console.log('home', props.userDetails);

const childCompRef = React.useRef(null);



  function getStepContent(step) {
    switch (step) {
      case 0:
        return <PurchaseHeader 
        // ref={childCompRef} 
        sendAttack={getAttack}
        nextPage={handleNext} 
        step={dataFlag} 
        userDetails={props.userDetails}/>;
      case 1:
        return <PurchaseMaterial
        sendAttack={getAttack2}
        sendAttack3={getAttack3}
        prevPage={handleBack}
        nextPage={handleNext}  
        userDetails={props.userDetails}
        header={headerData2}
        />;
      case 2:
        return <PurchaseOutput 
        header={headerData2} body={bodyData} header2={subTotalData}
        userDetails={props.userDetails}
        prevPage={handleBack}
        nextPage={handleNext}  
        />;
    }
  }

  React.useLayoutEffect(() => {
    console.log('childCompRef'+ childCompRef )
  }, [])

  const getAttack = (headerData) => {
    console.log('headerData1 ====' + headerData);

    console.log('headerData ====' + JSON.parse(headerData));
    setheaderData(JSON.parse(headerData));
    console.log('headerData ====', headerData2);
   
}

const getAttack2 = (data) => {
    console.log('headerData ====' + JSON.parse(data));
    setbodyData(JSON.parse(data));
    console.log('bodyData ====', bodyData);
   
}

const getAttack3 = (data) => {
    console.log('total ====' + JSON.parse(data));
    setsubTotalData(JSON.parse(data))
    console.log('subTotalData ====' + subTotalData);
    // setbodyData(JSON.parse(data));
    // console.log('totalData ====', bodyData);
   
}

  const isStepOptional = (step) => {
    return step === 1;
  };

  const isStepSkipped = (step) => {
    return skipped.has(step);
  };

  const handleNext = () => {
     setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleSkip = () => {
    if (!isStepOptional(activeStep)) {
      throw new Error("You can't skip a step that isn't optional.");
    }

    setActiveStep((prevActiveStep) => prevActiveStep + 1);
    setSkipped((prevSkipped) => {
      const newSkipped = new Set(prevSkipped.values());
      newSkipped.add(activeStep);
      return newSkipped;
    });
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  return (
    
    <div className={classes.root}>
    <Card>
    <CardContent>
      <Stepper activeStep={activeStep}>
        {steps.map((label, index) => {
          const stepProps = {};
          const labelProps = {};
          return (
            <Step key={label} {...stepProps}>
              <StepLabel {...labelProps}>{label}</StepLabel>
            </Step>
          );
        })}
      </Stepper>
      </CardContent>  
    </Card>
      
      <div>
        {activeStep === steps.length ? (
            setActiveStep(0)
        ) : (
          <div>
            <Typography className={classes.instructions}>{getStepContent(activeStep)}</Typography>
          </div>
         )} 
      </div>
    </div>
  );
}