import React from "react";
import clsx from "clsx";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import List from "@material-ui/core/List";
import CssBaseline from "@material-ui/core/CssBaseline";
import Typography from "@material-ui/core/Typography";
import Divider from "@material-ui/core/Divider";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import InboxIcon from "@material-ui/icons/MoveToInbox";
import MailIcon from "@material-ui/icons/Mail";
import { useHistory } from "react-router-dom";


import PurchaseOrder from '../pages/purchase/PurchaseOrder';
import PurchaseReport from '../pages/purchase/PurchaseReport';
import PurchaseRevised from '../pages/purchase/PurchaseRevised';
import logo from '../assets/Kumar_Electricals_Png.png';

// import routes from '../navlist/routes';

const drawerWidth = 240;

const routes = [
  {
    name: 'Header Fields',
    path: "/siteHome",
  //   exact: true,
    sideName: 'Generate PO',
  },
  {
    name: 'PO Revision',
    path: "/purchaseRevised",
    sideName: 'PO Revision',
  },
  {
    name: 'Report',
    path: "/purchaseReport",
    sideName: 'Master Database',
  }
];

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex"
  },
  appBar: {
    backgroundColor: "#ff6666",
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen
    })
  },
  appBarShift: {
    backgroundColor: "#ff6666",
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen
    })
  },
  menuButton: {
    marginRight: 36
  },
  hide: {
    display: "none"
  },
  drawer: {
    
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: "nowrap",
    backgroundColor: "#ff0000"
  },
  drawerOpen: {
    backgroundColor: "#ff8080",
    width: drawerWidth,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen
    })
  },
  drawerClose: {
    backgroundColor: "#ff6666",
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen
    }),
    overflowX: "hidden",
    width: theme.spacing(7) + 1,
    [theme.breakpoints.up("sm")]: {
      width: theme.spacing(9) + 1
    }
  },
  toolbar: {
    
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-end",
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3)
  },
  side: {
    backgroundColor: "#ff0000"
  }
}));

export default function PuchaseDashboard(props) {
  const classes = useStyles();
  const history = useHistory();
  const theme = useTheme();
  const [open, setOpen] = React.useState(true);
  const [fragment, setfragment] = React.useState(0);
  const [selectedIndex, setSelectedIndex] = React.useState(0);

  console.log('history location', history.location.state);

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  const loadFragment = () => {
      switch (fragment) {
          // case 'Home': return <ProductList />
          // case 'Bubblegum': return <Example/>
          // case 'shoelaces': return <div>hello welcome</div>
          // case 'Header Fields': return <PurchaseOrder userDetails={history.location.state}/>
          // // case 'Details': return <SiteDetails userDetails={history.location.state.userName}/>
          // case 'Report': return <PurchaseReport userDetails={history.location.state}/>
          case 0: return <PurchaseOrder userDetails={history.location.state}/>
          // case 'Details': return <SiteDetails userDetails={history.location.state.userName}/>
          case 1: return <PurchaseRevised userDetails={history.location.state}/>
          case 2: return <PurchaseReport userDetails={history.location.state}/>
      }
  }

const handleListItemClick = (event, index) => {
    setSelectedIndex(index);
    // setfragment(event.target.innerHTML)
    setfragment(index)
    console.log(index);
    console.log(event.target);
    console.log(event.target.innerHTML);
  };


  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar
        position="fixed"
        className={clsx(classes.appBar, {
          [classes.appBarShift]: open
        })}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            className={clsx(classes.menuButton, {
              [classes.hide]: open
            })}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" noWrap>
            PURCHASE MODULE
          </Typography>
        </Toolbar>
      </AppBar>
      <Drawer
        variant="permanent"
        className={clsx(classes.drawer, {
          [classes.drawerOpen]: open,
          [classes.drawerClose]: !open
        })}
        classes={{
          paper: clsx({
            [classes.drawerOpen]: open,
            [classes.drawerClose]: !open
          })
        }}
      >
        <div className={classes.toolbar}>
          {/* <div>Hello world this is robo site</div> */}
          <img src={logo} width="170" height="50"/>

          <IconButton onClick={handleDrawerClose}>
            {theme.direction === "rtl" ? (
              <ChevronRightIcon />
            ) : (
              <ChevronLeftIcon />
            )}
          </IconButton>
        </div>
        <Divider />
        <List>
          {routes.map((text, index) => (
            <ListItem  button key={text.name} 
            selected={selectedIndex === index}
            // onClick={e=>setfragment(text.name)}
            onClick={(event) => handleListItemClick(event, index)}
            >
              <ListItemIcon style={{color: '#ffffff'}}>
                {index % 2 === 0 ? <InboxIcon /> : <MailIcon />}
              </ListItemIcon>
              {/* <ListItemText primary={text} /> */}
              {/* <Link to={text.path}><ListItemText primary={text.name} /></Link> */}
              <ListItemText primary={text.sideName} style={{color: '#ffffff'}}/>
            </ListItem>
          ))}
        </List>
        <Divider />
      </Drawer>
      <main className={classes.content}>
        <div className={classes.toolbar} />
          {loadFragment()}
      </main>
    </div>
  );
}



