import React from "react";
import {
  BrowserRouter,
  Switch,
  Route,
  Link
} from "react-router-dom";

import routes from './navlist/routes';
import Login from './pages/Login';
import Dashboard from './pages/Dashboard';
import ProductList from './pages/ProductList';
import AppUser from './AppUser';
import MiniDrawer from './components/MiniDrawer';
import Example from './pages/Example';
// import AdminDashboard from './components/AdminDashboard2';
import AdminDashboard from './dashboard/AdminDashboard';
import SiteDahsboard from './dashboard/SiteDashboard';
import StoreDashboard from './dashboard/StoreDashboard';
import AccountDashboard from './dashboard/AccountDashboard';
import QuotationDashboard from './dashboard/QuotationDashboard';
import PurchaseDashboard from './dashboard/PurchaseDashboard';
import SiteHome from './pages/site/SIteHome';
import SiteDetails from './pages/site/SiteDetails';
import SiteReport from './pages/site/SiteReport';

import ProductAdd from './pages/admin/ProductAdd';
import AddDropdown from './pages/admin/AddDropdown';
import ProductUpdateDelete from './pages/admin/AddDropdown';
import RegisterUser from './pages/admin/RegisterUser';
import ProjectAdd from './pages/admin/ProjectAdd';
import Vender from './pages/admin/Vender';

import Receipt from './pages/store/Receipt';
import Issue from './pages/store/Issue';

import PurchaseOrder from './pages/purchase/PurchaseOrder';
import PurchaseReport from './pages/purchase/PurchaseReport';
import PurchaseRevised from './pages/purchase/PurchaseRevised';

import QuotationOrder from './pages/quotation/QuotationOrder';
import QuotationReport from './pages/quotation/QuotationReport';

import AccountHome from './pages/account/AccountHome';
import AccountHistory from "./pages/account/AccountHistory";


export default class AppMainNavigation extends React.Component {
    render() {
        return (
            <BrowserRouter>
                <Route exact path="/" component={Login}/>
                <Route path="/user" component={AppUser}/>
                <Route path="/dashboard" component={Dashboard}/>
                <Route path="/miniDrawer" component={MiniDrawer}/>
                <Route path="/product" component={ProductList}/>
                <Route path="/example" component={Example}/>

                <Route path="/siteDahsboard" component={SiteDahsboard}/>
                <Route path="/siteHome" component={SiteHome}/>
                <Route path="/siteDetails" component={SiteDetails}/>
                <Route path="/siteReport" component={SiteReport}/>

                <Route path="/storeDashboard" component={StoreDashboard}/>
                <Route path="/receipt" component={Receipt}/>
                <Route path="/issue" component={Issue}/> 

                <Route path="/accountDashboard" component={AccountDashboard}/>
                <Route path="/accountHome" component={AccountHome}/>
                <Route path="/accountHistory" component={AccountHistory}/>

                <Route path="/quotationDashboard" component={QuotationDashboard}/>
                <Route path="/quotationReport" component={QuotationReport}/>

                <Route path="/purchaseDashboard" component={PurchaseDashboard}/>
                <Route path="/purchaseOrder" component={PurchaseOrder}/>
                <Route path="/purchaseReport" component={PurchaseReport}/>
                <Route path="/purchaseRevised" component={PurchaseRevised}/>

                <Route path="/adminDashboard" component={AdminDashboard}/>
                <Route path="/prodcutAdd" component={ProductAdd}/>
                <Route path="/addDropdown" component={AddDropdown}/>
                <Route path="/productUpdateDelete" component={ProductUpdateDelete}/>
                <Route path="/registerUser" component={RegisterUser}/>
                <Route path="/projectAdd" component={ProjectAdd}/>
                <Route path="/vender" component={Vender}/>


            </BrowserRouter>
        )
    }
}