import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import SiteIndent from '../../components/SiteIndent';
import SiteIndentMaterial from '../../components/SiteIndentMaterial';
import SiteOutput from '../../components/SiteOutput';

import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import { PDFDownloadLink, Document, Page } from '@react-pdf/renderer'
// import MyDocument




const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
  },
  button: {
    marginRight: theme.spacing(2),
  },
  instructions: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
}));

function getSteps() {
  return ['Header Fields', 'Working Fields', 
  'Output'
];
}






export default function SiteHome(props) {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const [skipped, setSkipped] = React.useState(new Set());
  const [dataFlag, setDataFlag] = React.useState(false)
  const steps = getSteps();
  const [headerData2, setheaderData] = React.useState();
  const [bodyData, setbodyData] = React.useState();


console.log('home', props.userDetails);

const childCompRef = React.useRef(null);



  function getStepContent(step) {
    switch (step) {
      case 0:
        return <SiteIndent 
        // ref={childCompRef} 
        sendAttack={getAttack}
        nextPage={handleNext} 
        step={dataFlag} 
        userDetails={props.userDetails}/>;
      case 1:
        return <SiteIndentMaterial
        sendAttack={getAttack2}
        prevPage={handleBack}
        nextPage={handleNext}  
        userDetails={props.userDetails}
        />;
      case 2:
        return <SiteOutput header={headerData2} body={bodyData}
        prevPage={handleBack}
        nextPage={handleNext}  
        />;
    //   default:
    //     return 'Unknown step';
    }
  }

  React.useLayoutEffect(() => {
    console.log('childCompRef'+ childCompRef )
  }, [])

  const getAttack = (headerData) => {
    console.log('headerData ====' + JSON.parse(headerData));
    setheaderData(JSON.parse(headerData));
    console.log('headerData ====', headerData2);
   
}

const getAttack2 = (data) => {
    console.log('headerData ====' + JSON.parse(data));
    setbodyData(JSON.parse(data));
    console.log('bodyData ====', bodyData);
   
}

  const isStepOptional = (step) => {
    return step === 1;
  };

  const isStepSkipped = (step) => {
    return skipped.has(step);
  };

  const handleNext = () => {
    //  setDataFlag(true)
     // getStepContent(activeStep)
    //   getAttack();
    // let newSkipped = skipped;
    // if (isStepSkipped(activeStep)) {
    //   newSkipped = new Set(newSkipped.values());
    //   newSkipped.delete(activeStep);
    // }

     setActiveStep((prevActiveStep) => prevActiveStep + 1);
    // setSkipped(newSkipped);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleSkip = () => {
    if (!isStepOptional(activeStep)) {
      // You probably want to guard against something like this,
      // it should never occur unless someone's actively trying to break something.
      throw new Error("You can't skip a step that isn't optional.");
    }

    setActiveStep((prevActiveStep) => prevActiveStep + 1);
    setSkipped((prevSkipped) => {
      const newSkipped = new Set(prevSkipped.values());
      newSkipped.add(activeStep);
      return newSkipped;
    });
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  return (
    
    <div className={classes.root}>
    <Card>
    <CardContent>
      <Stepper activeStep={activeStep}>
        {steps.map((label, index) => {
          const stepProps = {};
          const labelProps = {};
        //   if (isStepOptional(index)) {
        //     labelProps.optional = <Typography variant="caption">Optional</Typography>;
        //   }
        //   if (isStepSkipped(index)) {
        //     stepProps.completed = false;
        //   }
          return (
            <Step key={label} {...stepProps}>
              <StepLabel {...labelProps}>{label}</StepLabel>
            </Step>
          );
        })}
      </Stepper>
      </CardContent>  
    </Card>
      
      <div>
        {activeStep === steps.length ? (
            setActiveStep(0)
        //   <div>
        //     <Typography className={classes.instructions}>
        //       All steps completed - you&apos;re finished
        //     </Typography>
        //     <Button onClick={handleReset} className={classes.button}>
        //       Reset
        //     </Button>
        //   </div>
        ) : (
          <div>
            <Typography component={'span'} className={classes.instructions}>{getStepContent(activeStep)}</Typography>
            {/* <div style={{ textAlign: 'right', marginTop: '6ch'}}>
            { activeStep !== 0 && activeStep !== 1 && activeStep !== steps.length? (
                <div>
              <Button disabled={activeStep === 0} onClick={handleBack} className={classes.button}>
                Back
              </Button>
             

              <Button
                variant="contained"
                color="primary"
                onClick={handleNext}
                className={classes.button}
              >
                {activeStep === steps.length - 1 ?  'Raise Indent' : 'Next'}
              </Button>
              </div>
) : ('')
}
            </div> */}
          </div>
         )} 
      </div>
    </div>
  );
}


