import React from "react";
import ListSubheader from "@material-ui/core/ListSubheader";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Collapse from "@material-ui/core/Collapse";
import InboxIcon from "@material-ui/icons/MoveToInbox";
import DraftsIcon from "@material-ui/icons/Drafts";
import SendIcon from "@material-ui/icons/Send";
import ExpandLess from "@material-ui/icons/ExpandLess";
import ExpandMore from "@material-ui/icons/ExpandMore";
import StarBorder from "@material-ui/icons/StarBorder";
import { Link, NavLink } from "react-router-dom";

export default React.memo(({ classes, item, getComponent, parentIndex }) => {
  const [open, setOpen] = React.useState();
  const [selectedIndex, setSelectedIndex] = React.useState();
//   const[activeStep, setactiveStep]= React.useState(true)
  const handleClick = (e) => {
      e.preventDefault();
    setOpen(!open);
    
  };


  const handleListItemClick = (event, index) => {
    console.log('event', event)
    setSelectedIndex(index);
    getComponent(event, parentIndex);
  };

  return (
    <>
      <ListItem button onClick={handleClick} >
        <ListItemIcon style={{color: '#ffffff'}}>
          {item.icon}
        </ListItemIcon>
        <ListItemText primary={item.name} style={{color: '#ffffff'}}/>
        {open ? <ExpandLess style={{color: '#ffffff'}}/> : <ExpandMore style={{color: '#ffffff'}}/>}
      </ListItem>
      <Collapse in={open} timeout="none"  >
        <List component="div" disablePadding>
          {item.items.map((i,index) => (
            <ListItem button className={classes.nested} 
            // component={NavLink} 
            // to={i.linkTo} 
            activeStyle={{backgroundColor:"green"}} 
            style={{paddingLeft: 50}}
            onClick={(event) => handleListItemClick(i, index)}
            >
              {/* <ListItemIcon>
               {i.icon}
              </ListItemIcon> */}
               <ListItemIcon style={{color: '#ffffff'}}>
                  {i.icon}
                </ListItemIcon>
              <ListItemText primary={i.name} style={{color: '#ffffff'}}/>
            </ListItem>
          ))}
        </List>
      </Collapse>
    </>
  );
});
