
import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
// import TextField from '@material-ui/core/TextField';
// import Card from '@material-ui/core/Card';
// import CardContent from '@material-ui/core/CardContent';
// import { InputLabel, Button } from '@material-ui/core';
// import Grid from '@material-ui/core/Grid';
import MaterialTable from "material-table";
import Autocomplete from '@material-ui/lab/Autocomplete';


export default function SiteIndentMaterial(props) {
    const { useState } = React;
    // const classes = useStyles();

    const [data, setData] = useState([
        { name: 'Mehmet', surname: 'Baran', birthYear: 1987, birthCity: 63 },
        { name: 'Zerya Betül', surname: 'Baran', birthYear: 2017, birthCity: 34 },
      ]);
return (
<MaterialTable
      title="Basic Validation Preview"
      columns={[
        {
          title: 'Name', field: 'name', validate: rowData => rowData.name !== '',
        },
        { title: 'Surname', field: 'surname', validate: rowData => rowData.surname.length > 3 },
        { title: 'Birth Year', field: 'birthYear', type: 'numeric', validate: rowData => rowData.birthYear > 1900 },
        {
          title: 'Birth Place',
          field: 'birthCity',
          lookup: { 34: 'İstanbul', 63: 'Şanlıurfa' },
        },
      ]}
      data={data}
      editable={{
        onRowAdd: newData =>
          new Promise((resolve, reject) => {
            setTimeout(() => {
              setData([...data, newData]);
              resolve();
            }, 1000)
          }),
        onRowUpdate: (newData, oldData) =>
          new Promise((resolve, reject) => {
            setTimeout(() => {
              const dataUpdate = [...data];
              const index = oldData.tableData.id;
              dataUpdate[index] = newData;
              setData(dataUpdate);

              resolve();
            }, 1000)
          }),
      }}
      options={{
        search: false,
        headerStyle: {fontWeight: 900,borderTopWidth: 5, borderTopColor: 'gray'},
        // pageSize: rowsPerPage,
        pageSizeOptions: [5, 10, 20, { value: data.length, label: 'All' }],
        // selection: true,
        //  showTitle: true,
       toolbar:false,
        // paging: false,
      }}
    />
)

}